import React from 'react';
import { CloudUrl } from '../../constants';
const defaultFinalLogo = `${CloudUrl}/images/8651481539701.51210.png`;
const urlImage = 'https://cloud.taggbox.com/';
const BannerLogo = ({ LogoImage, image_path }) => {
    return <img src={LogoImage.newName.includes('logo_bnr') ? LogoImage.newName :LogoImage.newName!=''? `${urlImage}${LogoImage.path}${LogoImage.newName}` :''} className="img-responsive" style={{ maxWidth: 240, width: "auto", paddingTop: 5, paddingBottom: 5, objectFit: "contain" }} />
}

export default BannerLogo;